import React from 'react'

export default class PosEnvio extends React.Component {
    render(){
        const {extras, envio} = this.props

        const {enviando} = extras
        const {resultado, erros} = envio

        const enviadoComSucesso = resultado !== undefined
        const erroNoEnvio = erros !== undefined

        if(!enviando && erroNoEnvio) {
            return(
                <div>
                    <div className="bg-light">
                        <h2 className="my-0 text-dark">Não conseguimos registrar seu requerimento</h2>
                    </div>
                    
                    <p>Tente voltar e verificar se os dados foram informados corretamente antes de tentar enviar novamente</p>
                </div>
            )
        }

        if(!enviando && enviadoComSucesso) {
            const {id} = resultado
            const link = `https://api-estagil-gm-dev.herokuapp.com/requerimentos/${id}/baixar`
            return(
                <div>
                    <div className="bg-light">
                        <h2 className="my-0 text-dark">Requerimento recebido :)</h2>
                    </div>
                    <ol>
                        <li>Clique <a href={link} target="_blank" rel="noopener noreferrer">aqui</a> para baixar seu requerimento</li>
                        <li>Imprima o arquivo baixado</li>
                        <li>Colete as assinaturas necessárias</li>
                        <li>Com este requerimento assinado, dirija-se ao NURI para receber sua documentação</li>
                    </ol>
                </div>
            )
        }

        return(
            <div>
                <div className="bg-light">
                    <h2 className="my-0 text-dark">Enviando...</h2>
                </div>
                
                <p>Aguarde um instante enquanto registramos seu requerimento<br/>
                
                {   
                    extras.envioDemorado >= 15 ? 'O envio está demorando muito mais que o normal, será que ocorreu algum problema com sua conexão à internet?' :
                    extras.envioDemorado >= 10 ? 'O envio está demorando mais que o normal, aguarde enquanto estamos tentando enviar os dados...' :
                    extras.envioDemorado >=  5 ? 'Só mais um pouquinho...' :
                    ''
                }
                </p> 
            </div>
        )
    }
}