import React from 'react'
import {Formulario} from '../forms-base'
import {Aluno, LocalDoEstagio, Orientacao, PosEnvio} from './Paginas'

export default class Requerimento extends React.Component {
    constructor(){
        super()
        this.state = {
            extras : {
                listaDeCursos : [],
                listaDeLocaisDeEstagio : [],
            },
        }

        this.adicionaExtra = this.adicionaExtra.bind(this)
        this.enviaDados = this.enviaDados.bind(this)
    }

    componentWillMount(){
        this.carregaCursos()
    }

    adicionaExtra(chave, valor){
        this.setState(prevState => (
            { extras : { ...prevState.extras, [chave] : valor } }
        ))
    }

    enviaDados(dados, envioComSucesso, envioComErros){
        // controle do tempo de envio
        this.adicionaExtra('enviando', true)
        setTimeout(()=> this.adicionaExtra('envioDemorado',  5),  5000 )
        setTimeout(()=> this.adicionaExtra('envioDemorado', 10), 10000 )
        setTimeout(()=> this.adicionaExtra('envioDemorado', 15), 15000 )

        const headers = new Headers()
        headers.set("Content-Type", "application/json")
        headers.set("Accept", "application/json")
        
        const requestParams = {
            method : 'POST',
            headers,
            mode : 'cors',
            body : JSON.stringify(dados)
        }

        fetch(`https://api-estagil-gm-dev.herokuapp.com/requerimentos`, requestParams)
            .then(response => {
                this.adicionaExtra('enviando', false)

                if(response.ok)
                    response.json().then(resposta => envioComSucesso(resposta))
                
                else
                    return response.text().then(Promise.reject.bind(Promise))
            })
            .catch(erros => {
                envioComErros(erros)
            })
    }

    carregaCursos() {
        fetch(`https://api-estagil-gm-dev.herokuapp.com/cursos`)
        .then(resposta => resposta.json())
        .then(listaDeCursos => this.setState(prevState => (
            { extras : {...prevState.extras, listaDeCursos} }
        )))
    }

    carregaLocaisDeEstagio(cursoId) {
        fetch(`https://api-estagil-gm-dev.herokuapp.com/locais_de_estagio/${cursoId}/curso_id`)
        .then(resposta => resposta.json())
        .then(listaDeLocaisDeEstagio => this.setState(prevState => (
            { extras : {
                ...prevState.extras,
                listaDeLocaisDeEstagio,
                carregaLocaisDeEstagio : false,
            }}
        )))
    }

    render() {
        const telas = [
            Aluno,
            LocalDoEstagio,
            Orientacao,
        ]
        const {extras} = this.state

        if(extras.carregaLocaisDeEstagio === true && extras.cursoSelecionado !== undefined){
            this.carregaLocaisDeEstagio(extras.cursoSelecionado)
        }

        return (
            <div className="container"> 
                <Formulario
                    telas={telas}
                    telaPostSubmit={PosEnvio}
                    extras={extras}
                    onSubmit={this.enviaDados} 
                    adicionaExtra={this.adicionaExtra}/>
            </div>
        )
    }
  }